@import "../../utils/Colors/Colors.module.css";

.ow {
  background-color: #efefef;
}
.w {
  background-color: white;
}
* {
  /* font-family: "Roboto Condensed", sans-serif; */
}
@keyframes boxShadowAnimation {
  0% {
    box-shadow: none;
  }
  50% {
    /* Add your desired box-shadow here */
    box-shadow: 0 0px 20px 0 rgba(0, 0, 0, 0.2);
  }
  100% {
    box-shadow: none;
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .image {
    width: 100%;
    /* padding-top: 4rem; */
    /* height: 50vh; */
  }

  .bannerCTA {
    position: absolute;
    top: 38%;
    left: 70%;
    /* transform: translate(-50%, -50%); */
    padding: 5px 5px;
    background-color: #2574ad; /* Set your desired background color */
    color: #fff; /* Set your desired text color */
    text-decoration: none;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
  }

  .imgContent {
    color: white;
    margin-top: -4rem;
  }
  .section1 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
    text-align: left;
    font-size: 18px;
  }
  .container {
    margin: 0rem 1.5rem;
    text-align: left;
  }

  .section1 p,
  .section7 p {
    font-size: 18px;
    color: var(--primary-color);
  }
  .section2 {
    padding: 1rem;
    margin: 0.5rem;
    margin-top: 2.5rem;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0 rgba(0, 0, 0, 0.5);
  }
  .section3 {
    padding: 1rem 1rem 0rem 1rem;
    /* margin: 0rem 1.5rem; */
    border-radius: 10px;
  }
  .section3 li {
    font-size: 18px;
    color: var(--primary-color);
    padding: 10px 0px 10px 30px;
  }
  .section3 span {
    color: #f6663b;
  }
  .section3 li::before {
    content: "✔️";
    position: absolute;
    left: 40px;
    right: auto;
  }
  .section3 hr {
    border-color: rgb(255, 255, 255);
  }
  .section4 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5);
  }

  .section4 form {
    text-align: center;
    padding: 1rem 0rem;
    border-radius: 10px;
  }
  .label {
    text-align: center;
    font-weight: 600;
    margin-top: 1rem;
  }

  .section4 h3 {
    text-align: center;
    color: var(--primary-color);
    font-size: 20px;
  }
  .section4 p {
    text-align: left;
    color: var(--primary-color);
    font-size: 18px;
    padding: 0rem 1rem;
  }
  .accordianTitle {
    background-color: #efefef;
    padding: 1rem 1rem 2.5rem 1rem;
    margin: 1rem 3rem;
    margin-bottom: -2.5rem;
    border-radius: 10px;
    font-size: 18px;
    color: var(--primary-color);
  }
  .accordianContent {
    background-color: #fafafa;
  }
  .formContainer {
    /* background-color: #fafafa; */
    border-radius: 10px;
    padding: 1rem 0rem;
  }

  .section4 form input {
    padding: 0.5rem 1rem;
    max-width: 60vw;
    background-color: white;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
    border-radius: 10px;
    font-family: "Roboto Condensed", sans-serif;
    color: var(--primary-color);
  }

  .section4 form input::placeholder {
    color: var(--primary-color);
  }

  input[type="file"]::file-selector-button {
    margin-right: 20px;
    border: none;
    background-image: linear-gradient(315deg, #152c56 0%, #1e9afe 200%);
    padding: 10px 20px;
    border-radius: 10px;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }

  input[type="file"]::file-selector-button:hover {
    transform: scale(0.75);
    filter: none;
  }

  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 2rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
  .section5 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    margin-top: 1rem;
    /* box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5); */
    /* margin: 0rem 1.5rem; */
  }
  .section5Container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .section5image {
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
    margin: 1rem;
  }

  .section5 label {
    font-size: 18px;
    color: var(--primary-color);
    font-weight: 900;
    margin: 1rem;
  }

  .section6 {
    padding: 1rem 1rem 1rem 1rem;
    /* margin: 0rem 1.5rem; */
    border-radius: 10px;
    /* padding-top: 1rem; */
    /* margin-top: 1rem; */
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }

  .section7 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    /* box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5); */
    text-align: left;
    font-size: 18px;
    margin-top: 1rem;
    /* padding-top: 1rem; */
  }
  .section7Description {
    margin: 0rem 1.5rem;
  }
  .section7 span {
    font-style: italic;
    font-weight: 500;
    font-size: 26px;
    text-align: left;
    color: #f6663b;

    /* margin: 1rem 0rem; */
  }
  .faqSection {
    margin-top: 1rem;
  }
}

