/* EXTRA LARGE DEVICES (LARGE LAPTOPS AND DESKTOPS, 1200PX AND UP) */
@media only screen and (min-width: 1200px) {
  .image {
    width: 100%;
    object-fit: cover;
    height: 100vh;
    /* margin-top: 0.5rem; */
    margin-top: -1rem;
    object-position: calc(100% - 0px) calc(0% + 20px);
  }

  .bannerCTA {
    left: 80%;
    top: 102%;
    scale: 1;
    transition: scale 0.3s;
  }
  .bannerCTA:hover {
    scale: 0.75;
  }

  .section1 {
    padding: 1rem;
  }
  .section1 div,
  .section3 div,
  .section5 div,
  .section7 div {
    margin: 0rem 4rem;
  }
  .container {
    margin: 0rem 8rem;
  }
  .section1 p {
    margin-left: 4rem;
    margin-right: 4rem;
    font-size: 22px;
  }

  .section3 li {
    padding: 10px 0px 10px 65px;
    font-size: 22px;
  }
  .section3 hr {
    max-width: 60vw;
  }
  .section3 li::before {
    content: "✔️";
    /* color: red; */
    left: 110px;
  }
  .section4 p {
    text-align: center;
  }
  .section4 form {
    /* max-width: 80vw; */
    /* margin: 0 auto; */
    /* padding-left: 10rem; */
    /* padding-right: 10rem; */
  }
  .formContainer {
    padding-left: 6rem;
    padding-right: 6rem;
  }
  .section4 h3,
  .section4 p {
    text-align: left;
    margin-left: 8rem;
  }
  .section4 p {
    margin-left: 7rem;
  }
  .accordianTitle {
    margin: 0rem 14rem;
  }
  .accordianContent {
    background-color: #fafafa;
  }
  .section5Container {
    display: flex;
    flex-direction: row;
    /* align-items: center; */
  }

  /* .section5image:hover {
    width: 40vw;
    height: 50vh;
    border-radius: 10px;
    cursor: pointer;
  } */
  .section6 {
    padding-top: 1rem;
  }
  .section7Description {
    display: flex;
    justify-content: center;
  }
  .section7 span {
    margin-right: 1rem;
    transform: rotate(0deg);

    transition: transform 0.5s ease; /* Smooth out the start/end of the hover action */
  }
  .section7 span:hover {
    cursor: pointer;
    animation: spin 0.5s linear; /* The animation will complete over 2 seconds in a smooth, continuous loop */
  }
  .section7 p {
    margin-right: 3rem;
    font-size: 24px;
  }
