/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
.ow {
  background-color: #efefef;
}

@media only screen and (max-width: 600px) {
  .image {
    width: 100%;
    /* height: 50vh; */
  }
  .section1 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
    text-align: left;
    font-size: 18px;
  }
  .section1 p {
    color: var(--primary-color);
  }

  .container {
    margin: 0rem 1.5rem;
  }
  .section2 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    margin-top: 1rem;
  }

  .section2 form {
    text-align: left;
    border-radius: 10px;
    margin: 0rem 1.5rem;
  }

  .section2 h4 {
    text-align: left;
    color: var(--primary-color);
    font-size: 18px;
  }

  .section2 form input {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }

  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 0.5rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
  .faqSection {
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
    margin-top: 1rem;
  }
}

/*  SMALL DEVICES (PORTRAIT TABLETS AND LARGE PHONES, 600PX AND UP) */
@media only screen and (min-width: 600px) {
  .image {
    width: 100%;
    object-fit: cover;
    margin-top: -4rem;
    height: 100vh;
    object-position: calc(100% + 200px) calc(0% + 45px);
  }
  .section1 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
    text-align: left;
    font-size: 18px;
  }
  .section1 p {
    color: var(--primary-color);
  }
  .container {
    margin: 0rem 1.5rem;
  }
  .section2 {
    padding: 0rem 1rem 1rem 1rem;
    margin-top: 1rem;
    border-radius: 10px;
  }

  .section2 form {
    text-align: left;
    border-radius: 10px;
    margin: 0rem 1.5rem;
  }

  .section2 h4 {
    text-align: left;
    color: var(--primary-color);
    font-size: 18px;
  }

  .section2 form input {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }

  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 0.5rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active,
  .button:hover {
    transform: scale(0.75);
    filter: none;
  }
  .faqSection {
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
    margin-top: 1rem;
  }
}
/* MEDIUM DEVICES (LANDSCAPE TABLETS, 768PX AND UP) */
@media only screen and (min-width: 768px) {
}
/* LARGE DEVICES (LAPTOPS/DESKTOPS, 992PX AND UP) */
@media only screen and (min-width: 992px) {
  .image {
    height: 100vh;
    object-fit: cover;
    /* margin-top: -4.5rem; */
    /* margin-top: 5rem; */
    object-position: calc(100% - 0px) calc(0% + 50px);
  }
}
/* EXTRA LARGE DEVICES (LARGE LAPTOPS AND DESKTOPS, 1200PX AND UP) */
@media only screen and (min-width: 1200px) {
  .image {
    height: 100vh;
    object-fit: cover;
    /* margin-top: -4.5rem; */
    /* margin-top: 5rem; */
    object-position: calc(100% - 0px) calc(0% + 50px);
  }
  .section1 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
    text-align: left;
    font-size: 22px;
  }

  .section1 div,
  .section2 div,
  .section2 h4,
  .section1 p {
    margin: 0rem 4rem;
  }
  .section2 form {
    text-align: left;
    margin: 0rem 4rem;
  }
  .button {
    margin-left: 4rem;
  }
}
