/* FAQ.module.css */
.faq {
  margin: 15px;
  padding: 15px;
  background-color: rgb(255, 255, 255);
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}
.faq:hover {
  cursor: pointer;
}

.faqQuestion {
  position: relative;
  font-size: 20px;
  padding-right: 80px;
  transition: all 1s ease;
}

.faqQuestion::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0px;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  content: "➕";
  /* background-image: url("../../Images/logos/wheel_9_colored.png"); */
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  transition: all 1s ease;
}

.faqAnswer {
  opacity: 0;
  max-height: 0;
  overflow-y: hidden;
  transition: all 0.5s ease-out;
  /* text-align: left; */
  margin-top: 1rem;
  font-size: 18px;
  /* margin-left: 0.5rem; */
}

.open .faqQuestion::after {
  transform: translateY(-50%) rotate(225deg);
}

.open .faqAnswer {
  max-height: 1000px;
  opacity: 1;
  transition: max-height 1s ease; /* Adjusted for smoother transition. */
}
