/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
.ow {
  background-color: #efefef;
}
.w {
  background-color: white;
}
@keyframes boxShadowAnimation {
  0% {
    box-shadow: none;
  }
  50% {
    /* Add your desired box-shadow here */
    box-shadow: 0 0px 20px 0 rgba(0, 0, 0, 0.2);
  }
  100% {
    box-shadow: none;
  }
}

@media only screen and (max-width: 600px) {
  .section1 {
    padding: 1rem 0rem 0rem 1rem;
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5);
    padding-top: 4rem;
  }
  .title {
    /* margin-bottom: -2rem; */
  }
  .container {
    margin: 0rem 1.5rem;
  }

  .section1 p {
    margin-bottom: 2rem;
    text-align: left;
    font-size: 18px;
    color: var(--primary-color);
    margin-bottom: -2rem;
  }

  .sliderImage {
    width: 90vw;
    height: 60vh;
    object-fit: cover;
  }
  .section2 {
    /* margin-bottom: 0rem; */
  }
  .section3 {
    margin-top: -6rem;
    margin-bottom: 2.5rem;
  }
  .section3 div {
    margin-top: 1rem;
  }
  .section3 ul {
    margin: 1rem;
    margin-top: 2.5rem;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5);
  }
  .options {
    padding: 0.5rem 4rem;
    margin: 0rem;
  }
  .options:active,
  .options:hover {
    background-color: var(--primary-color);
    color: white;
    font-weight: 600;
    border-radius: 10px;
    cursor: pointer;
  }

  .description,
  .features {
    text-align: left;
    list-style: none;
    padding: 1rem 1rem 1rem 1rem;
    margin: 1rem;
    margin-top: 2.5rem;
    border-radius: 10px;
    background-color: #efefef;
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5);
    animation: boxShadowAnimation 3s infinite;
  }
  .exterior,
  .interior {
    width: 100%;
    object-fit: cover;
    object-position: center;
    margin-bottom: -0.5rem;
  }
  .section4 {
    padding: 0rem 1rem 1rem 1rem;
    margin: 1rem;
    margin-top: 1rem;
    border-radius: 10px;
    /* box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5); */
  }
  .socialTitle {
    margin: 0 auto;
  }

  .section4 img {
    border-radius: 50%;
    width: 2.4rem;
    margin: 1rem 0.7rem;
    transition: scale 0.3s;
    scale: 1;
  }

  .section4 img:hover,
  .section4 img:active {
    scale: 1.2;
  }
  .section5 {
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5);
  }

  .section7 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5);
  }

  .section7 form {
    text-align: center;
    border-radius: 10px;
    margin: 0rem 1.5rem;
  }

  .section7 h4 {
    text-align: left;
    color: var(--primary-color);
    font-size: 18px;
    margin-left: 1.5rem;
  }

  .section7 form input {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }
  .inputContainer {
    text-align: left;
  }
  .buttonContainer {
    text-align: left;
    margin-left: 1.5rem;
  }
  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 0.5rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
}

