@import "../../utils/Colors/Colors.module.css";

/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .section {
    background-color: white;
    border-radius: 10px;
    padding: 0rem 1rem 1rem 1rem;
    margin-top: 1rem;
  }
  .section div {
    margin: 0rem 1.5rem;
  }
  .section form {
    text-align: center;
    padding: 1rem 0.5rem;
    border-radius: 10px;
  }
  .section input {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }

  .section p {
    text-align: left;
    color: var(--primary-color);
    font-size: 18px;
  }

  .section form select {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }

  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active,
  .button:hover {
    transform: scale(0.75);
    filter: none;
  }
}

