/*Desktop View*/
.header {
  display: flex;
  background: linear-gradient(to right, #979797, #152c56 50%);
  justify-content: space-between;
  align-items: center;
  /* padding: 0px 40px; */
  height: 70px;
  position: fixed;
  width: 100vw;
  z-index: 1;
}
.logo-nav {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  /* margin-left: 50rem; */
}
.logo {
  width: 14rem;
  height: 3rem;
  margin-right: 3rem;
  /* margin-left: 30rem; */
  /* margin-left: -5rem; */
}
.nav-options {
  /* padding-left: 25px; */
  display: grid;
  /* grid-template-columns: repeat(8, auto); */
  display: flex;
  /* grid-gap: 50px; */
  list-style-type: none;
  margin-left: 2rem;
  /* transition: all 0.3s; */
  /* color: white; */
  /* scale: 1; */
}
.mobile-option {
  display: none;
}
.nav-options:hover {
  /* cursor: pointer;
  color: #f6663b;
  scale: 0.75; */
}

.option a {
  color: white;
  padding-left: 25px;
  font-weight: 600;
  scale: 1;
  transition: all 0.3s;
}

.option a:hover {
  cursor: pointer;
  color: #f6663b;
  scale: 0.85;
}
.signin-up {
  display: flex;
  padding: 0px 5px;
  list-style-type: none;
}
.sign-in {
  padding-right: 50px;
  align-self: center;
}
.sign-in :hover {
  color: white;
}
.signup-btn {
  padding: 10px 10px;
  height: 2.4rem;
  border-radius: 3px;
  background: rgb(222, 9, 241);
  color: white;
  cursor: pointer;
  align-items: center;
}
.mobile-menu {
  display: none;
}
@media (max-width: 992px) {
  /*Mobile View */
  .header {
    /* padding: 0px 10px; */
    width: 100vw;
    height: 60px;
    position: relative;
    margin-bottom: -4rem;
  }
  .logo {
    width: 9rem;
    height: 2rem;
    margin-right: 2rem;
    /* display: inline-block; */
    /* margin-left: 0; */
  }
  .nav-options {
    display: flex;
    width: 100%;
    height: 350px;
    position: absolute;
    top: 60px;
    left: -100%;
    opacity: 0;
    transition: all 0.5s ease;
    flex-direction: column;
    list-style-type: none;
    grid-gap: 0px;
    margin-left: 0rem;
  }
  .nav-options.active {
    background: linear-gradient(to right, #979797, #152c56 50%);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    align-content: center;
    padding-left: 0px;
    height: max-content;
    /* margin-left: -1.5rem; */
  }

  .menu-icon {
    width: 45px;
    height: 45px;
    color: white;
    padding-left: 10px;
  }
  .option {
    /* display: flex; */
    /* align-items: start; */
    /* margin: 0 auto; */
    /* justify-content: left; */
    /* margin: 0 auto; */
    /* text-align: center; */
    margin-left: -5vw;
    /* height: 10vw; */
    /* padding: 30px 0px; */
  }
  /* .sign-up {
    background: rgb(222, 9, 241);
    border-radius: 3px;
    color: white;
    padding: 20px 0px;
    width: 80%;
    align-self: center;
    border-radius: 3px;
  } */

  /* .signin-up {
    display: none;
  } */
}
.mobile-menu {
  display: block;
}
