@import "../../utils/Colors/Colors.module.css";
@import "../../utils/Fonts.css";
@keyframes boxShadowAnimation {
  0% {
    box-shadow: none;
  }
  50% {
    /* Add your desired box-shadow here */
    box-shadow: 0 0px 20px 0 rgba(0, 0, 0, 0.2);
  }
  100% {
    box-shadow: none;
  }
}
/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .section1 {
    padding: 0rem 1rem 1rem 1rem;
    background-color: #efefef;
    margin-top: 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }
  .section1 div {
    margin: 0rem 1.5rem;
  }
  .section1 p {
    font-size: 18px;
    color: var(--primary-color);
    text-align: left;
  }
  .section1 strong {
    color: #f6663b;
  }
  .description {
    margin: 1rem;
    margin-left: 2.5rem;
    text-align: left;
    font-family: "Roboto Condensed", sans-serif;
    font-size: 18px;
    color: var(--primary-color);
  }
  .span {
    font-size: 2rem;
    font-style: italic;
    color: #f6663b;
  }

  .btn {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    scale: 1;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .btn:active,
  .btn:hover {
    transform: scale(0.75);
    filter: none;
  }
}

