@import "../../utils/Colors/Colors.module.css";

.ow {
  background-color: white;
  /* box-shadow: none; */
}
.w {
  background-color: #efefef;
}
@keyframes popInOut {
  0%,
  100% {
    transform: scale(1); /* Start and end at normal size */
    opacity: 1;
  }
  50% {
    transform: scale(1.1); /* At half-time, grow the element */
    opacity: 1;
  }
}
@keyframes boxShadowAnimation {
  0% {
    box-shadow: none;
  }
  50% {
    /* Add your desired box-shadow here */
    box-shadow: 0 0px 20px 0 rgba(0, 0, 0, 0.2);
  }
  100% {
    box-shadow: none;
  }
}

/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  /* SECTION-1 */

  .section1 {
    /* height: 17.5rem; */
  }
  .image {
    width: 100%;
    /* padding-top: 4rem; */
    /* height: 50vh; */
  }
  .imageContainer {
  }
  .container {
    margin: 0rem 1.5rem;
  }

  /* SECTION-2 */

  .section2 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    /* box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5); */
    text-align: left;
    margin-top: 1rem;
    font-size: 18px;
  }

  .section2 a {
    color: var(--primary-color);
    font-weight: bold;
    font-size: 18px;
  }

  .whatsappIcon,
  .callIcon {
    border-radius: 50%;
    width: 25px;
    margin-left: 0.5rem;
  }

  .section2 p,
  .section3 p {
    color: var(--primary-color);
    font-size: 18px;
  }
  .section2 span,
  .section3 span {
    display: block;
    margin: 0rem 1.5rem;
  }

  /* SECTION-3 */

  .section3 {
    color: var(--primary-color);
    padding: 0rem 1rem 1rem 1rem;
    text-align: left;
    margin-top: 1rem;

    border-radius: 10px;
    font-size: 18px;
  }
  .section3 span {
    margin-top: 0.5rem;
  }

  .section3 a {
    font-weight: bolder;
    color: var(--primary-color);
  }

  /* SECTION-4 */

  .section4 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5);
  }

  .section4 form {
    text-align: center;
    border-radius: 10px;
    margin: 0rem 1.5rem;
  }

  .section4 h4 {
    text-align: left;
    color: var(--primary-color);
    font-size: 18px;
    margin-left: 1.5rem;
  }

  .section4 form input {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }
  .inputContainer {
    text-align: left;
  }
  .buttonContainer {
    text-align: left;
    margin-left: 1.5rem;
  }
  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 0.5rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
  /* SECTION-5 */

  .section5 {
    padding: 0rem 1rem 1rem 1rem;
    margin: 1rem;
    /* margin-top: 2.5rem; */
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.5);
    animation: boxShadowAnimation 3s infinite;
  }

  .section5 img {
    border-radius: 50%;
    width: 2.4rem;
    margin: 1rem 0.7rem;
    transition: scale 0.3s;
    scale: 1;
    animation: popInOut 2s infinite;
  }

  .section5 img:hover,
  .section5 img:active {
    scale: 1.2;
  }
  /* SECTION-6 */

  .section6 {
    border-radius: 10px;
    margin-top: 2.5rem;
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }

  .map {
    width: 100%;
    height: 20rem;
    border: 1px solid var(--primary-color);
    border-radius: 10px;
  }
  .dropdownContainer {
    text-align: left;
    margin: 0rem 1.5rem;
  }
  .dropdown {
    margin: 0rem 1rem 2rem 0rem;
    padding: 1rem;
    font-size: 1rem;
    color: black;
    font-weight: 500;
    filter: drop-shadow(2px 5px 5px #6c88bc);
    border: 1px solid var(--primary-color);
  }
  .mapSection {
    padding: 1rem;
  }
  .mapContainer {
    padding: 1rem 0rem;
  }
  .mapLabel {
    color: var(--primary-color);
    text-align: center;
    font-weight: 600;
    font-size: 18px;
    padding-bottom: 1rem;
  }
}

