.ow {
  background-color: #efefef;
}

.section1,
.section3,
.section4 {
  padding: 1rem;
  box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}
.section5 {
  padding: 1rem;
}

.section1 div,
.section2 div,
.section3 div,
.section4 div,
.section5 div {
  margin: 0rem 1.5rem;
}
.section2,
.section4 {
  padding: 1rem;
}

.section2 input,
.section3 input,
.section4 input,
.section5 input {
  color: var(--primary-color);
  border: none;
  border-bottom: 1px solid var(--primary-color);
  margin: 1rem;
  font-size: 18px;
}
.section1 button,
.section2 button,
.section3 button,
.section4 button,
.section5 button {
  padding: 0.5rem;
  width: 12.5rem;
  font-size: 1.2rem;
  margin-top: 0.5rem;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  background-image: linear-gradient(315deg, #152c56 0%, #1e9afe 200%);
  color: white;
  transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
}
.section1 button:hover,
.section1 button:active,
.section2 button:hover,
.section2 button:active,
.section3 button:hover,
.section3 button:active,
.section4 button:hover,
.section4 button:active,
.section5 button:hover,
.section5 button:active {
  transform: scale(0.75);
}
.section3 h3 {
  margin: 1rem;
  /* padding: 1rem; */
}
.label {
  text-align: center;
  font-size: 18px;
  font-weight: 900;
  margin-top: 1rem;
}
.span {
  color: red;
}
.li {
  text-align: left;
}
.h3 {
  text-align: left;
  margin-top: 1rem;
  margin-bottom: -0.5rem;
}
