/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .section1 {
    padding: 0rem 1rem 1rem 1rem;
    margin-top: 1rem;
    /* border-radius: 10px; */
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    /* box-shadow: 0px -8px 8px 0 rgba(0, 0, 0, 0.5); */
    background-color: white;
  }
  .section1 div {
    margin: 0rem 1.5rem;
  }
  .section1 p {
    text-align: left;
    font-size: 18px;
    color: var(--primary-color);
  }
  .section2 {
    background-color: white;
    margin-top: -2rem;
    padding: 1.5rem;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
}

/*  SMALL DEVICES (PORTRAIT TABLETS AND LARGE PHONES, 600PX AND UP) */
@media only screen and (min-width: 600px) {
  .section1 {
    padding: 0rem 1rem 1rem 1rem;
    margin-top: 1rem;
    /* border-radius: 10px; */
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    /* box-shadow: 0px -8px 8px 0 rgba(0, 0, 0, 0.5); */
    background-color: white;
  }
  .section1 div {
    margin: 0rem 1.5rem;
  }
  .section1 p {
    text-align: left;
    font-size: 18px;
    color: var(--primary-color);
  }
  .section2 {
    background-color: white;
    /* margin-top: -2rem; */
    /* padding: 1.5rem; */
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    scale: 1;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active,
  .button:hover {
    transform: scale(0.75);
    filter: none;
  }
  .card {
    display: inline-block;
    margin: 1rem;
  }
}
