@import "../../utils/Colors/Colors.module.css";

@keyframes popInOut {
  0%,
  100% {
    transform: scale(1); /* Start and end at normal size */
    opacity: 1;
  }
  50% {
    transform: scale(1.3); /* At half-time, grow the element */
    opacity: 1;
  }
}
/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .footer {
    padding: 0.5rem;
    background: linear-gradient(to right, #979797, #152c56 50%);
  }
  .footer a {
    color: white;
  }
  .icon {
    font-size: 1.8rem;
    margin-right: 0.5rem;
    color: white;
    transition: color 0.3s;
  }
  .logo {
    height: 2rem;
    width: 9rem;
    text-align: right;
  }
  .container {
    font-size: 18px;
    color: white;
    text-align: left;
    margin: 1rem;
  }
  .email,
  .info {
    font-size: 20px;
    color: white;
    text-align: left;
    margin: 1rem;
  }
  .p {
    font-size: 18px;
    color: white;
    text-align: left;
    margin: 0.2rem 1rem;
  }
  .inputContainer {
    text-align: left;
    margin: 0rem 1rem;
  }
  .input {
    margin: 0.5rem 0rem;
    color: var(--primary-color);
    font-size: 14px;
  }
  .input::placeholder {
    color: var(--primary-color);
  }
  .sendIcon {
    font-size: 1.8rem;
    margin-left: 0.5rem;
    display: inline-block;
    animation: popInOut 2s infinite;
  }
  .info li {
    margin: 0.5rem 0rem;
    font-size: 16px;
  }
  .link:hover,
  .link:active,
  .icon:hover,
  .icon:active {
    color: #f6663b;
    scale: 1.5;
  }

  .followLogo {
    border-radius: 50%;
    width: 2.4rem;
    margin: 1rem 0.7rem;
    transition: scale 0.3s;
    scale: 1;
    border: none;
  }

  .followLogo:hover,
  .followLogo:active {
    scale: 1.2;
  }
  .rightsReserved {
    font-size: 1rem;
    color: white;
    padding-bottom: 2rem;
  }
  .copyrightLogo {
    color: white;
  }
  .developer {
    color: white;
    margin-top: -1rem;
    padding-bottom: 1rem;
  }
  .developer a:hover,
  .developer a:active {
    color: #f6663b;
  }
}

/*  SMALL DEVICES (PORTRAIT TABLETS AND LARGE PHONES, 600PX AND UP) */
@media only screen and (min-width: 600px) {
  .footer {
    padding: 0.5rem;
    background: linear-gradient(to right, #979797, #152c56 50%);
  }
  .footer a {
    color: white;
  }
  .icon {
    font-size: 1.8rem;
    margin-right: 0.5rem;
    color: white;
    transition: color 0.3s;
  }
  .logo {
    height: 2rem;
    width: 9rem;
    text-align: right;
  }
  .container {
    font-size: 18px;
    color: white;
    text-align: left;
    margin: 1rem;
    transition: color 0.3s;
  }
  .email,
  .info {
    font-size: 20px;
    color: white;
