  }
  .container a:hover {
    color: #f6663b;
    scale: 1.5;
  }

  .followLogo {
    border-radius: 50%;
    width: 2.4rem;
    margin: 1rem 0.7rem;
    transition: scale 0.3s;
    scale: 1;
    border: none;
  }

  .followLogo:hover,
  .followLogo:active {
    scale: 1.2;
  }
  .rightsReserved {
    font-size: 1rem;
    color: white;
    padding-bottom: 2rem;
  }
  .copyrightLogo {
    color: white;
  }
  .developer {
    color: white;
    margin-top: -1rem;
    padding-bottom: 1rem;
  }
  .developer a {
    transition: color 0.3s;
  }
  .developer a:hover,
  .developer a:active {
    color: #f6663b;
  }
}
