/*
=============== 
Variables
===============
*/
@import "../../utils/Colors/Colors.module.css";

:root {
  /* dark shades of primary color*/
}
/*
  =============== 
  Global Styles
  ===============
  */

*,
::after,
::before {
  margin: 0;
  padding: 0;
  /* box-sizing: border-box; */
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  /* background: var(--clr-grey-10); */
  color: var(--clr-grey-1);
  line-height: 1.34;
  /* font-size: 0.875rem; */
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  margin-bottom: 1.25rem;
  color: var(--clr-grey-5);
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}
/*  global classes */

/* section */
/* App.module.css */

.section {
  /* width: 90vw; */
  margin: 0rem auto;
  margin-top: 0rem;
  max-width: var(--max-width);
}

.title {
  text-align: center;
  margin-bottom: 2rem;
}

.title h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
}

.title span {
  color: var(--clr-primary-5);
  font-weight: 700;
}

.sectionCenter {
  margin: 0 auto;
  margin-top: 4rem;
  width: 80vw;
  height: 450px;
  max-width: 800px;
  text-align: center;
  position: relative;
  display: flex;
  overflow: hidden;
}

.personImg {
  border-radius: 10%;
  margin-bottom: 1rem;
  width: 300px;
  height: 300px;
  object-fit: cover;
  border: 4px solid var(--clr-grey-8);
  box-shadow: var(--dark-shadow);
}

article h4 {
  text-transform: uppercase;
  color: var(--clr-primary-5);
  margin-bottom: 0.25rem;
  font-size: 1.5rem;
}

.text {
  max-width: 35em;
  margin: 0 auto;
  line-height: 2;
  color: var(--clr-grey-5);
}

.icon {
  font-size: 5rem;
  margin-top: -1rem;
  color: var(--clr-primary-5);
  transform: rotateY(180deg);
}

.prev,
.next {
  position: absolute;
  top: 150px;
  background: var(--clr-grey-5);
  color: var(--clr-white);
  width: 1.25rem;
  height: 1.25rem;
  display: grid;
  place-items: center;
  border-color: transparent;
  font-size: 1rem;
  margin: 0rem 1rem;
  border-radius: var(--radius);
  cursor: pointer;
  transition: var(--transition);
}

.prev:hover,
.next:hover {
  background: var(--clr-primary-5);
}

.prev {
  left: 0;
}

.next {
  right: 0;
}

.button {
  padding: 0.5rem;
  width: 12.5rem;
  font-size: 1.2rem;
  margin-top: 2rem;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  background: linear-gradient(to right, #979797, #152c56 50%);
  color: white;
  transition: background-color 0.3s, color 0.3s, transform 0.3s;
}
.button:active {
  transform: scale(0.75);
  filter: none;
}
@media (min-width: 800px) {
  .text {
    max-width: 45em;
  }

  .prev,
  .next {
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
  }
}

article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: var(--transition);
}

article.activeSlide {
  opacity: 1;
  transform: translateX(0);
}

article.lastSlide {
  transform: translateX(-100%);
}

article.nextSlide {
  transform: translateX(100%);
}
