/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
.ow {
  background-color: #efefef;
}

@media only screen and (max-width: 600px) {
  .section1 {
    padding-top: 4rem;
  }
  .image {
    height: 50vh;
    width: 100vw;
  }
  .section2 {
    padding: 1rem;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }
  .section3 {
    padding: 1rem;
  }
  .section3 label {
    /* margin-left: 3rem; */
    text-align: center;
    font-size: 18px;
    font-weight: 900;
    color: var(--primary-color);
  }

  .container {
    margin: 0rem 1.5rem;
  }
  .section2 p {
    font-size: 18px;
    text-align: left;
    color: var(--primary-color);
  }
  .section3 form {
    text-align: center;
    padding: 1rem 0.5rem;
    border-radius: 10px;
  }
  .section3 input {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }
  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    scale: 1;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
}

/*  SMALL DEVICES (PORTRAIT TABLETS AND LARGE PHONES, 600PX AND UP) */
@media only screen and (min-width: 600px) {
  .image {
    object-fit: cover;
    width: 100%;
    height: 100vh;
    object-position: calc(100% - 0px) calc(0% + 20px);
  }
  .container {
    margin: 0rem 1.5rem;
  }
  .section2 {
    padding: 0rem 1rem 1rem 1rem;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }
  .section3 {
    padding: 1rem;
  }
  .section3 label {
    /* margin-left: 12vw; */
    text-align: center;
    font-size: 18px;
    font-weight: 900;
    color: var(--primary-color);
  }
  .section2 p {
    font-size: 18px;
    text-align: left;
    color: var(--primary-color);
  }
  .section3 form {
    text-align: center;
    padding: 1rem 0.5rem;
    border-radius: 10px;
  }
  .section3 input {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }
  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active,
  .button:hover {
    transform: scale(0.75);
    filter: none;
  }
}
