/* EXTRA LARGE DEVICES (LARGE LAPTOPS AND DESKTOPS, 1200PX AND UP) */
@media only screen and (min-width: 1200px) {
  .image {
    object-fit: cover;
    width: 100%;
    height: 100vh;
    object-position: calc(100% - 0px) calc(0% - 40px);
  }
  .section2 div,
  .section3 div {
    margin: 0rem 4rem;
  }
  .section2 p {
    margin: 0rem 4rem;
    font-size: 22px;
  }
  .section3 input {
    font-size: 22px;
  }
  .calculatorContainer {
    display: flex;
    align-items: flex-start;
  }
  .section3 form {
    margin-left: 5vw;
  }
  .section4 {
    margin-left: 15vw;
  }
}
