/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
.ow {
  background-color: #efefef;
}
@media only screen and (max-width: 600px) {
  .image {
    width: 100%;
    /* padding-top: 4rem; */
  }
  .section2 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
    text-align: left;
    font-size: 18px;
  }

  .container {
    margin: 0rem 1.5rem;
  }
  .section2 p {
    font-size: 18px;
    color: var(--primary-color);
  }
  .section3 {
    padding: 1rem;
    text-align: left;
  }

  .section3 li {
    background-color: rgb(212, 212, 212);
    color: black;
    padding: 0.5rem;
    transition: color 0.3s, background-color 0.3s;
    margin: 0;
  }
  .section3 li:hover,
  .section3 li:active {
    background-color: black;
    color: white;
    font-weight: 600;
  }

  .section4 {
    /* margin-bottom: 1rem; */
    margin-top: -1rem;
    border-radius: 10px;
  }
  .faqSection {
    margin-top: 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }

  /* SEARCH SECTION */

  .searchSection {
    background-color: #efefef;
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }

  .container {
    margin: 0rem 1.5rem;
  }
  .searchSection p {
    color: var(--primary-color);
    text-align: left;
    font-size: 18px;
    /* margin: 0; */
  }
  .accordionContainer {
    padding: 0rem 1.5rem 1rem 1.5rem;
  }

  .dropdown {
    color: var(--primary-color);
    margin: 1rem auto;
    width: 16rem;
    padding: 1rem;
    font-size: 18px;

    border-radius: 5px;
  }
  .searchSection form {
    text-align: center;
    padding: 1rem 0.5rem;
    border-radius: 10px;
  }
  .searchSection form strong {
    color: #f6663b;
  }

  .searchSection form h4 {
    text-align: left;
    color: var(--primary-color);
    font-size: 18px;
  }

  .searchSection form select {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }

  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active,
  .button:hover {
    transform: scale(0.75);
    filter: none;
  }
}

