  }
  .section2 p {
    margin-left: 4rem;
    margin-right: 4rem;
    font-size: 22px;
  }

  /* SEARCH SECTION */

  .searchSection div {
    margin: 0rem 4rem;
  }
  .searchSection p {
    margin-left: 4rem;
    margin-right: 4rem;
    font-size: 22px;
  }

  .searchSection form {
    display: flex;
    margin: 0rem 2rem;
  }
  .button {
    margin-top: 0rem;
  }
}
