/* EXTRA LARGE DEVICES (LARGE LAPTOPS AND DESKTOPS, 1200PX AND UP) */
@media only screen and (min-width: 1200px) {
  .image {
    width: 100%;
    /* object-fit: fill; */
    height: 60vh;
    /* margin-top: 0.5rem; */
    margin-top: 1rem;
    object-position: calc(100% - 100%) calc(0% - 0px);
    /* object-position: center; */
  }
  .imageContainer {
    margin-top: -1.5rem;
  }
  .banner {
    margin-bottom: 0.5rem;
    /* padding-bottom: -50rem; */
  }
}
