/* LoginPage.module.css */

@import "../../utils/Colors/Colors.module.css";
@keyframes moveBackground {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .loginContainer {
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    padding: 1rem 3rem;
    padding-top: 4rem;
    /* width: 80vw; */
    /* height: 300px; */
    text-align: center;
    /* margin: 5 auto; */
    margin: 0rem 1rem 2rem 1rem;
    /* margin-top: 50px; */
  }

  h2 {
    margin-top: 0;
  }

  .formGroup {
    margin: 10px 0;
  }

  label {
    display: block;
    text-align: left;
  }

  input {
    width: 70%;
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
  }

  .button {
    padding: 0.5rem;
    display: inline-block;
    width: 8rem;
    margin: 0rem 1rem;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background-image: linear-gradient(315deg, #152c56 0%, #1e9afe 200%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }

  .button:hover {
    background-color: #0056b3;
  }
}

