@import "../../utils/Colors/Colors.module.css";

.ow {
  background-color: #efefef;
}
.w {
  background-color: white;
}
/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .section1 {
    padding: 1rem;
    padding-top: 4rem;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }

  .container {
    margin: 0rem 1.5rem;
  }

  .section1 p {
    font-size: 18px;
    text-align: left;
    color: var(--primary-color);
  }
  .section2 {
    padding: 1rem;
  }

  .section2 form {
    text-align: center;
    border-radius: 10px;
  }

  .section2 form input {
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }

  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 0.5rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
}
/* MEDIUM DEVICES (LANDSCAPE TABLETS, 768PX AND UP) */
@media only screen and (min-width: 600px) {
  .section1 {
    padding: 1rem;
    padding-top: 4rem;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }

  .container {
    margin: 0rem 1.5rem;
  }
  .section1 p {
    font-size: 18px;
    text-align: left;
    color: var(--primary-color);
  }
  .section2 {
    padding: 1rem;
  }

  .section2 form {
    text-align: center;
    border-radius: 10px;
  }

  .section2 form input {
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }

  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 0.5rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active,
  .button:hover {
    transform: scale(0.75);
    filter: none;
  }
}
/* LARGE DEVICES (LAPTOPS/DESKTOPS, 992PX AND UP) */
@media only screen and (min-width: 992px) {
}
/* EXTRA LARGE DEVICES (LARGE LAPTOPS AND DESKTOPS, 1200PX AND UP) */
@media only screen and (min-width: 1200px) {
  .container {
    margin: 0rem 8rem;
  }
  .section1 {
    padding-top: 5rem;
  }
}
