@import "../../utils/Colors/Colors.module.css";
/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .carTitle {
    color: var(--primary-color);
    text-align: left;
    margin: 10px;
    font-weight: 900;
  }
  .data p {
    font-size: 20px;
    color: var(--primary-color);
  }

  .title {
    color: transparent;
    padding: 1rem;
    /* margin-left: 1.5rem; */
    /* text-align: left; */
    margin-bottom: 1.5rem;
  }
  .description {
    color: white;
    text-align: left;
    margin-left: 10px;
    font-weight: 600;
    font-family: "Roboto Condensed", sans-serif;
  }
  .imageContainer {
    /* width: 13.2rem; */
    height: 14rem;
    /* margin-top: -34px; */
  }
  #image {
    /* height: 100%; */
    width: 70vw;
    height: 80vh;
    object-fit: cover;
    border-radius: 10px;
  }
  .button {
    padding: 0.5rem 1rem;
    font-weight: bolder;
    color: var(--primary-color);
    border-radius: 5px;
    border: none;
    margin-top: 0.5rem;
    background-color: white;
    font-family: "Roboto Condensed", sans-serif;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
  .carouselContainer {
    width: 30%;
    height: 26.5rem;
    margin: 0 auto;
    margin-top: -4.5rem;
    /* background: linear-gradient(to bottom, #efefef, #152c56 45%); */
  }
  .strong {
    color: #f6663b;
  }
}

