/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .flipCard {
    filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.5));
    width: 300px;
    height: 420px;
    perspective: 1000px;
    margin: 0 auto;
    margin-top: 2rem;
  }

  .flipCardInner {
    position: relative;
    width: 100%;
    height: 100%;
    align-items: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
  }

  .flipCard:hover .flipCardInner {
    transform: rotateY(180deg);
  }

  .flipCardFront,
  .flipCardBack {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  .flipCardInner,
  .flipCardFront {
    background-color: #efefef;
    align-items: center;
    justify-content: center;
    color: black;
  }

  .flipCardBack {
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transform: rotateY(180deg);
    text-align: center;
  }
  .navImg {
    width: 300px;
    height: 250px;
    object-fit: cover;
  }
  .cardInnerText {
    color: #383838;
  }
  .carTitle {
    color: white;
    text-align: left;
    margin: 10px;
    margin-bottom: 5px;
    font-size: 30px;
    margin: 1rem;
    font-family: "Roboto Condensed", sans-serif;
    font-weight: 900;
  }
  .description {
    color: white;
    text-align: left;
    margin: 1rem;
    font-size: 21px;
    font-family: "Roboto Condensed", sans-serif;
    font-weight: 900;
  }
  .button {
    width: 2.2rem;
    border-radius: 50%;
    color: var(--primary-color);
    border: none;
    margin-top: 1rem;
    background-color: white;
    font-family: "Roboto Condensed", sans-serif;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
  .titleContainer {
    font-family: "Roboto Condensed", sans-serif;
    padding: 0.5rem;
    margin: 1rem;
    margin-top: -0.5rem;
    background: linear-gradient(to right, #979797, #152c56 50%);
    transform: skew(-10deg, 0deg);
    font-size: 19px;
    color: white;
  }
  .title {
    font-weight: 600;
  }
  .descriptionContainer {
    display: flex;
    margin: 10px;
    justify-content: space-between;
  }
  .showMore {
    padding: 0.5rem 1rem;
    background-color: white;
    color: black;
    border-radius: 10px;
    font-size: 16px;
    border: 1px solid hsl(0, 0%, 94%);
  }
  .showMore:hover,
  .showMore:active {
    scale: (1.2);
  }
}

