@keyframes scroll-left {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-70%);
  }
}
@keyframes scroll-left-large-screens {
  0% {
    transform: translateX(300%);
  }
  100% {
    transform: translateX(-200%);
  }
}

.banner {
  width: 100%;
  background-color: #efefef;
  overflow: hidden;
  position: relative;
  white-space: nowrap;
  padding-top: 0.5rem;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
}

.bannerText {
  display: inline-block;
  animation: scroll-left 10s linear infinite;
  font-size: 20px;
  font-weight: bold;
}
.bannerText span {
  color: #f6663b;
}
.bannerText p {
  display: inline;
  margin-right: 10px;
  margin-left: 5px;
  color: var(--primary-color);
}

/* Repeat the content */
.bannerContent {
  display: flex;
  width: calc(500% + 40rem); /* Adjust based on content */
}

@media only screen and (min-width: 600px) {
  .banner {
    /* padding-bottom: 2.5rem; */
  }
  .bannerText {
    /* padding-top: 0.5rem; */
  }
  .bannerContent {
    width: 10vw; /* Adjust based on content */
  }
  .bannerText {
    animation: scroll-left-large-screens 30s linear infinite;
  }
}

@media only screen and (min-width: 1200px) {
  .banner {
    /* padding-bottom: 2.5rem; */
  }
  .bannerText {
    padding-top: 0.5rem;
  }
  .bannerContent {
    width: 100%; /* Adjust based on content */
  }
  .bannerText {
    animation: scroll-left-large-screens 30s linear infinite;
  }
}
