* {
  font-family: "Roboto Condensed", sans-serif;
}
@import "../../utils/Colors/Colors.module.css";

.ow {
  background-color: #efefef;
}
.w {
  background-color: white;
}
/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .image {
    width: 100%;
    /* padding-top: 4rem; */
    /* height: 50vh; */
  }

  .section2 {
    padding: 0rem 1rem 1rem 1rem;
    text-align: left;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }
  .descriptionImage {
    width: 80vw;
    margin-bottom: 1rem;
    border-radius: 10px;
  }
  .section2 span,
  .section3 span,
  .section4 span {
    display: block;
    position: relative;
  }
  .section2 span::before,
  .section3 span::before,
  .section4 span::before {
    content: "";
    position: absolute;
    border-radius: 10px;
    width: 80vw;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    /* background: linear-gradient(to bottom, rgb(72, 72, 72), rgba(0, 0, 0, 0)); */
    z-index: 1;
    pointer-events: none; /* Allows clicking through the overlay to the image */
  }

  .container {
    margin: 0rem 1.5rem;
  }

  .section2 p,
  .section3 p,
  .section4 p,
  .section6 p {
    color: var(--primary-color);
    font-size: 18px;
  }

  .section3 {
    padding: 0rem 1rem 1rem 1rem;
    text-align: left;
    border-radius: 10px;
  }

  .section4 {
    padding: 0rem 1rem 1rem 1rem;
    text-align: left;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }

  .section5 {
    padding: 1rem 1rem 1rem 1rem;
  }
  .section5TitleContainer {
    margin: 0rem 1.5rem;
  }
  .section6 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    text-align: left;
    margin-top: -2rem;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }
  .section6 p {
    margin-top: 1rem;
  }
  .cardImage {
    width: 100%;
    border-radius: 10px;
  }

  .section7 {
    padding: 0rem 1rem 1rem 1rem;
    /* margin-top: -2rem; */
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }
  .section7TitleContainer {
    margin: 0rem 1.5rem;
  }
}

