/* EXTRA LARGE DEVICES (LARGE LAPTOPS AND DESKTOPS, 1200PX AND UP) */
@media only screen and (min-width: 1200px) {
  .image {
    height: 100vh;
    object-fit: cover;
    object-position: calc(100% - 0px) calc(0% - 0px);
  }
  .section2 div,
  .section3 div,
  .section4 div,
  .section6 div {
    margin: 0rem 4rem;
  }

  .section2 p,
  .section3 p,
  .section4 p,
  .section6 p {
    margin-left: 4rem;
    margin-right: 4rem;
  }
  .section5TitleContainer {
    margin: 0rem 8rem;
  }
  .imageContainer {
    margin: 0rem 8rem;
  }
  .cardImage {
    width: 60%;
    margin: 0rem 4rem;

    /* height: 80vh; */
  }
  .section7CarouselContainer {
