.ow {
  background-color: #efefef;
}

/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .section1 {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    /* background-color: #f7f7f7; */
    background-color: #efefef;
    text-align: left;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
  }
  .titleContainer {
    margin: 0rem 1.5rem;
  }

  .button {
    padding: 0.5rem;
    /* display: inline-block; */
    width: 6rem;
    margin: 0rem 0.5rem;
    font-size: 16px;
    /* margin-top: 0.5rem; */
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background-image: linear-gradient(315deg, #152c56 0%, #1e9afe 200%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }

  ul {
    list-style: none;
    padding: 0;
  }

  li {
    margin: 1rem;
    font-size: 18px;
  }
  .section3 {
    padding: 1rem 0rem;
    margin: 1rem 0rem;
    border-radius: 10px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
  }
  .section3 div {
    /* margin: 0rem 1.5rem; */
  }
  .section3 ul {
    text-align: left;
  }
  .accordionTitle {
    margin: 0;
  }
  .buttonContainer {
    display: flex;
    margin-left: 2rem;
  }
}

