@import "../../utils/Colors/Colors.module.css";
@keyframes boxShadowAnimation {
  0% {
    box-shadow: none;
  }
  50% {
    /* Add your desired box-shadow here */
    box-shadow: 0 0px 20px 0 rgba(0, 0, 0, 0.2);
  }
  100% {
    box-shadow: none;
  }
}
/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .section1 {
    background-color: #efefef;
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }

  .container {
    margin: 0rem 1.5rem;
  }
  .section1 p {
    color: var(--primary-color);
    text-align: left;
    font-size: 18px;
    /* margin: 0; */
  }
  .accordionContainer {
    padding: 0rem 1.5rem 1rem 1.5rem;
  }

  .dropdown {
    color: var(--primary-color);
    margin: 1rem auto;
    width: 16rem;
    padding: 1rem;
    font-size: 18px;

    border-radius: 5px;
  }
  .section1 form {
    text-align: center;
    padding: 1rem 0.5rem;
    border-radius: 10px;
  }
  .section1 form strong {
    color: #f6663b;
  }

  .section1 form h4 {
    text-align: left;
    color: var(--primary-color);
    font-size: 18px;
  }

  .section1 form select {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }

  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    font-weight: 600;

    margin-top: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active,
  .button:hover {
    transform: scale(0.75);
    filter: none;
  }
}

