@import "../../utils/Colors/Colors.module.css";

/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
@media only screen and (max-width: 600px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: #fff;
    position: fixed;
    z-index: 3;
    width: 100%;
  }

  .menuButton {
    background-color: transparent;
    border: none;
    color: #fff;
    cursor: pointer;
    font-size: 1.6rem;
    width: 1.5rem;
  }

  .modalBackdrop {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    visibility: hidden; /* Initially hidden */
    opacity: 0; /* Initially transparent */
    transition: visibility 0s linear, opacity 0.5s linear; /* Smooth transition for opacity */
  }

  .showModalBackdrop {
    visibility: visible; /* Make visible when active */
    opacity: 1; /* Fully opaque */
    transition: visibility 0s linear 0s, opacity 0.5s linear;
  }

  .modalContent {
    background-color: #fff;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
    width: 100vw;
    transform: translateX(-50%) translateY(-100%);
    transition: transform 0.5s ease-in-out;
  }
  .showModalBackdrop .modalContent {
    transform: translateX(0%) translateY(0);
  }

  .menu {
    list-style: none;
    text-align: left;
    font-family: "Roboto Condensed", sans-serif;
  }

  .menu li {
    margin-bottom: 0.5rem;
    color: #333;
  }

  .menu a {
    text-decoration: none;
  }

  .closeButton {
    background-color: var(--primary-color);
    border: none;
    color: #fff;
    padding: 0.5rem 1rem;
    margin-top: 0.5rem;
    cursor: pointer;
    border-radius: 6px;
  }
  .logo {
    width: 9rem;
    height: 2rem;
    margin-right: 2rem;
  }
}

/*  SMALL DEVICES (PORTRAIT TABLETS AND LARGE PHONES, 600PX AND UP) */
@media only screen and (min-width: 600px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.8rem;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: #fff;
    position: fixed;
    z-index: 3;
    width: 100%;
  }

  .menuButton {
    background-color: transparent;
    border: none;
    color: #fff;
    cursor: pointer;
    font-size: 1.6rem;
    width: 1.5rem;
  }

  .modalBackdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    /* transform: translateY(-100%); */

    transition: transform 0.5s ease;
  }

  .modalContent {
    background-color: #fff;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
    width: 100vw;
    transform: translateY(-100%);
    transition: transform 0.5s ease;
    z-index: 0;
  }
  .showModal {
    transform: translateY(0);
  }

  .menu {
    list-style: none;
    text-align: left;
    font-family: "Roboto Condensed", sans-serif;
  }

  .menu li {
    margin-bottom: 0.5rem;
    color: #333;
  }

  .menu a {
    text-decoration: none;
  }

  .closeButton {
    background-color: var(--primary-color);
    border: none;
    color: #fff;
    padding: 0.5rem 1rem;
    margin-top: 0.5rem;
    cursor: pointer;
    border-radius: 6px;
  }
  .logo {
    width: 9rem;
    height: 2rem;
    margin-right: 2rem;
  }
}
/* MEDIUM DEVICES (LANDSCAPE TABLETS, 768PX AND UP) */
@media only screen and (min-width: 768px) {
  .navbar {
    justify-content: space-between;
    /* z-index: 1; Adjust alignment */
  }

  .logo {
    /* margin-left: 1rem; */
    width: 9rem;
    height: 2rem;
    margin-right: 2rem;

    /* Add margin to the logo */
  }
  .logo:hover {
    cursor: pointer;
  }

  .menu {
    display: flex; /* Display the menu items side by side */
    align-items: center; /* Align items vertically */
    list-style: none; /* Remove list styles */
    padding: 0;
    margin-right: 1rem;
    margin-left: 1rem;
  }

  .menu li {
    margin-right: 1.5rem;
    font-size: 18px;
    font-weight: 900;
    scale: 1;
    transition: all 0.3s;
  }
  .menu li:hover {
    scale: 0.85;
    color: #f6663b;
  }

  .link {
    color: #ffffff;
    transition: color 0.5s;
  }
  .link:hover {
    color: #f6663b;
  }

  .modalBackdrop,
  .modalContent {
    display: none; /* Hide the modal for this screen size */
  }
}
/* LARGE DEVICES (LAPTOPS/DESKTOPS, 992PX AND UP) */
@media only screen and (min-width: 992px) {
}
/* EXTRA LARGE DEVICES (LARGE LAPTOPS AND DESKTOPS, 1200PX AND UP) */
@media only screen and (min-width: 1200px) {
  .logo {
    /* margin-left: 1rem; */
    width: 14rem;
    height: 3rem; /* Add margin to the logo */
  }
}

