/* EXTRA SMALL DEVICES (PHONES, 600PX AND DOWN) */
.ow {
  background-color: #efefef;
}
@keyframes popInOut {
  0%,
  100% {
    transform: scale(1); /* Start and end at normal size */
    opacity: 1;
  }
  50% {
    transform: scale(1.1); /* At half-time, grow the element */
    opacity: 1;
  }
}

@media only screen and (max-width: 600px) {
  .image {
    width: 100%;
    /* padding-top: 4rem; */
    /* height: 50vh; */
  }
  .section2 {
    padding: 0rem 1rem 1rem 1rem;
    text-align: left;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }

  .container {
    margin: 0rem 1.5rem;
  }
  .section2 ul {
    margin: 0rem 1.5rem;
  }
  .section2 li {
    color: var(--primary-color);
    font-size: 18px;
    /* animation: popInOut 2s infinite; */
  }
  .link {
    transition: color 0.3s;
  }
  .link:hover,
  .link:active {
    color: #f6663b;
  }
  .section3 {
    /* padding: 1rem; */
    /* box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5); */
    border-radius: 10px;
  }

  .section4 {
    border-radius: 10px;
    padding: 0rem 1rem 1rem 1rem;
  }
  .section6 form,
  .section4 form {
    text-align: center;
    border-radius: 10px;
    margin: 0rem 1.5rem;
  }
  .inputContainer {
    text-align: left;
  }

  .section4 h3 {
    margin-top: 1rem;
    font-size: 18px;
    font-weight: 900;
  }

  .section4 h4 {
    text-align: left;
    color: var(--primary-color);
    font-size: 18px;
  }

  .section6 form input,
  .section4 form input {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
  }
  .dropdown {
    padding: 0.5rem 1rem;
    margin: 0.5rem 0rem;
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid var(--primary-color);
    max-width: 66vw;
  }

  .button {
    padding: 0.5rem;
    width: 12.5rem;
    font-size: 1.2rem;
    margin-top: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(to right, #979797, #152c56 50%);
    color: white;
    transition: background-color 0.3s, color 0.3s, transform 0.3s; /* Add transitions to background-color, color, and transform */
  }
  .button:active {
    transform: scale(0.75);
    filter: none;
  }
  .section6 {
    padding: 0rem 1rem 1rem 1rem;
    border-radius: 10px;
    text-align: left;
    margin-top: 1rem;
    box-shadow: 0px 0px 20px 0 rgba(0, 0, 0, 0.5);
  }
  .section6 p {
    font-size: 18px;
    color: var(--primary-color);
    /* margin: 0rem 1.5rem; */
    margin-top: 1rem;
  }
  .cardImage {
    width: 80%;
    border-radius: 10px;
    margin: 0rem 1.5rem;
  }

  .section4 {
    margin-top: 1rem;
  }
}

