/* EXTRA LARGE DEVICES (LARGE LAPTOPS AND DESKTOPS, 1200PX AND UP) */
@media only screen and (min-width: 1200px) {
  .image {
    height: 100vh;
    object-fit: cover;
    object-position: top;
  }
  .section2 div {
    margin: 0rem 4rem;
  }
  .section2 ul {
    margin: 0rem 4rem;
  }
  .section2 li {
    font-size: 22px;
  }

  .section3 h4 {
    margin: 0rem 7rem;
  }
  .inputContainer {
    margin: 0rem 7rem;
  }
  .container {
    margin: 0rem 8rem;
  }
  .cardImage {
    width: 60%;
    border-radius: 10px;
    margin-left: 8rem;
  }
  .section6 p {
    /* margin-left: 8rem; */
    font-size: 22px;
  }
  .section6 strong {
    font-size: 22px;
  }
  .section4 {
    padding-top: 1rem;
  }
